import './button.css';

interface ButtonProps {
    onClick: () => void;
    disabled?: boolean;
    className?: string;
    children: React.ReactNode;
    primary?: boolean;
    label?: string;
    size: 'small' | 'medium' | 'large';
    backgroundColor?: string;
    name: string;
    mode: any;
    style?: {};
    variant:'primary'|'success'|'danger'|'warning'
}

import React from 'react';

const handleClick = () => {
    alert('Button clicked!');
};

