const Button = ({variant}: ButtonProps) => {
    return (
        <button
            style={{
                color: 'white',
                backgroundColor: 'skyblue',
                height: 30,
                width: 70,
                borderRadius: 10
            }}
            type="button"
            name="ButtonName"
            onClick={handleClick}
            // disabled={disabled}
            // className={['storybook-button', `storybook-button--${size}`, mode].join(' ')}
        >
            {children}
            Click Me
        </button>
    );
};

export default Button;
